.hero {
  padding: 70px 0 80px;
  position: relative;
  &__container {
    padding-right: 40px; }
  &__title {
    position: absolute;
    left: 0;
    top: 250px;
    font-family: mr_ArenqG, sans-serif;
    font-size: 440px;
    color: #FFFFFF;
    opacity: 0.5;
    font-weight: 400;
    line-height: 450px;
    z-index: -1;
    &::after {
      content: '';
      position: absolute;
      right: -53px;
      bottom: 113px;
      width: 50px;
      height: 50px;
      background-color: transparent;
      border: 4px solid #FFFFFF;
      border-radius: 100%; } }
  &__subtitle {
    position: relative;
    font-family: "Sofia Pro Regular", sans-serif;
    font-size: 180px;
    line-height: 81.5%;
    letter-spacing: -8px;
    font-weight: 400;
    margin-bottom: 300px; }
  &__text {
    max-width: 683px;
    font-size: 24px;
    line-height: 140%;
    margin-bottom: 30px; }
  &-social {
    display: flex;
    justify-content: flex-end;
    &__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-size: 18px;
      line-height: 18px;
      color: black;
      text-transform: uppercase;
      &:not(:last-child) {
        margin-right: 30px; } } }
  &-link {
    display: inline-block;
    padding: 12px 52px;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
    background-color: transparent;
    border: 1.5px solid #292828; }
  &__link {
    margin-bottom: 35px; } }

