*, h1, p {
  margin: 0;
  padding: 0; }

* {
  box-sizing: border-box; }

body {
  background-color: #dedcd4;
  font-family: "Sofia Pro Light", sans-serif; }

.container {
  width: 77%;
  max-width: 1100px;
  height: 100%;
  margin-left: 23%; }

@font-face {
  font-family: "Sofia Pro Bold";
  src: url("../fonts/SofiaProBold.woff2") format("woff2"), url("../fonts/SofiaProBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Sofia Pro Light";
  src: url("../fonts/SofiaProLight.woff2") format("woff2"), url("../fonts/SofiaProLight.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Sofia Pro Medium";
  src: url("../fonts/SofiaProMedium.woff2") format("woff2"), url("../fonts/SofiaProMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Sofia Pro Regular";
  src: url("../fonts/SofiaProRegular.woff2") format("woff2"), url("../fonts/SofiaProRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "mr_ArenqG";
  src: url("../fonts/19694.woff2") format("woff2"), url("../fonts/19694.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

.sidebar {
  height: 100vh;
  width: 23%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 92px 40px 80px;
  position: fixed;
  z-index: 10; }
  .sidebar-nav {
    display: flex;
    flex-direction: column; }
    .sidebar-nav__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-weight: 400;
      font-size: 31px;
      line-height: 31px;
      color: #000000;
      margin-bottom: 6px; }
  .sidebar-logo__link {
    font-family: "Sofia Pro Bold", sans-serif;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    text-decoration: none;
    color: black; }

.hero {
  padding: 70px 0 80px;
  position: relative; }
  .hero__container {
    padding-right: 40px; }
  .hero__title {
    position: absolute;
    left: 0;
    top: 250px;
    font-family: mr_ArenqG, sans-serif;
    font-size: 440px;
    color: #FFFFFF;
    opacity: 0.5;
    font-weight: 400;
    line-height: 450px;
    z-index: -1; }
    .hero__title::after {
      content: '';
      position: absolute;
      right: -53px;
      bottom: 113px;
      width: 50px;
      height: 50px;
      background-color: transparent;
      border: 4px solid #FFFFFF;
      border-radius: 100%; }
  .hero__subtitle {
    position: relative;
    font-family: "Sofia Pro Regular", sans-serif;
    font-size: 180px;
    line-height: 81.5%;
    letter-spacing: -8px;
    font-weight: 400;
    margin-bottom: 300px; }
  .hero__text {
    max-width: 683px;
    font-size: 24px;
    line-height: 140%;
    margin-bottom: 30px; }
  .hero-social {
    display: flex;
    justify-content: flex-end; }
    .hero-social__link {
      font-family: "Sofia Pro Regular", sans-serif;
      font-size: 18px;
      line-height: 18px;
      color: black;
      text-transform: uppercase; }
      .hero-social__link:not(:last-child) {
        margin-right: 30px; }
  .hero-link {
    display: inline-block;
    padding: 12px 52px;
    text-transform: uppercase;
    text-decoration: none;
    color: black;
    background-color: transparent;
    border: 1.5px solid #292828; }
  .hero__link {
    margin-bottom: 35px; }

@media screen and (min-width: 1550px) {
  .container {
    max-width: 1440px;
    margin-left: 23%; }
  .hero__title {
    position: absolute;
    left: 10%; }
    .hero__title::after {
      right: -53px; } }

@media screen and (max-width: 1355px) {
  .hero__title {
    top: calc(100vw / 7);
    font-size: calc(100vw / 3.2); }
    .hero__title::after {
      display: none;
      right: -53px; }
  .hero__subtitle {
    font-size: calc(100vw / 8);
    margin-bottom: calc(100vw/4.2); } }
